import React from "react";
import "../components/About.css";
import p5 from "../images/profile.jpg";

const About = () => {
   return (
    <div className="main">
      <div className="about">
         <div className="content">
            <h1 id="about">About Us</h1>
         </div>
         <div className="profile">
            <div className="imgg">
               <img src={p5} alt="" />
            </div>
            <div className="profile_content">
               <h1>Director - Jambanna</h1>
               <p>
                  Jambanna has been with the rice export business since 2014 and
                  has considerable experience in purchases of rice from various
                  intermediaries in the business. He has been successful in
                  building up export business and make Sri Laxmi Enterprises as
                  one of the largest exporters of rice in the country.
               </p>
            </div>
         </div>
         <div className="our_mission">
          <div className="title">
            <h1>Our Mission</h1>
          </div>
          <div className="mission-content">
            <p>As one of the world's leading Rice exporters, we are committed to deliver precision-perfect quality to our customers nurture a professional work environment, which fosters employee excellence, growth and job satisfaction and build a financially strong, growth oriented company that creates value for our .</p>
          </div>
         </div>
      </div>
      </div>
   );
};

export default About;
