import React from 'react';
import '../components/Contact.css'
// import p3 from "../images/Product1.jpg";
import p4 from "../images/quote.png";

const Contact = () => {
  return (
    <div className="main_container">
      <div className="bg">
        <h1>Contact</h1>
      </div>
      <div className="part5">
            <form
               action="https://formsubmit.co/2eb5c5e764b2f09780c4ece550fa63a4"
               method="POST"
            >
               <div class="input">
                  <input type="text" name="Name" placeholder="Name" />
               </div>
               <div class="input">
                  <input
                     type="email"
                     name="email"
                     placeholder="Email Address"
                     required
                  />
               </div>
               <div class="input-txt">
                  <textarea id="message" rows="5"></textarea>
               </div>
               <button className="button bt">Submit</button>
            </form>
            <div className="contact-details">
               <div className="d">
                  <i><img src={p4} alt="" /></i>
                  <h3>Address</h3>
                  <p>Shop No. 15 Plot No. 10 NPP Complex, Rajendra Gunj, Raichur-58410</p>
               </div>
               <div className="d">
                  <i><img src={p4} alt="" /></i>
                  <h3>Contact</h3>
                  <p>9845554549 / 7676022132</p>
               </div>
               <div className="d">
                  <i><img src={p4} alt="" /></i>
                  <h3>Email</h3>
                  <p>sales@shrilakshmienterprises.com</p>
               </div>
            </div>
         </div>
    </div>
  )
}

export default Contact