import React from "react";
import "./Home.css";
import { Link } from "react-router-dom";

const Home = () => {
   return (
      <div className="home">
         <div className="content">
            <h1>Varsha Enterprises</h1>
            <h2>Quality Rice Assured</h2>
            <Link to="/contact">
               <button className="button">Contact Us</button>
            </Link>
         </div>
      </div>
   );
};

export default Home;
