import React from "react";
import '../components/Products.css'
import p from '../images/Product1.jpg'


const Product = () => {
   return (
      <div className="main_container">
         <div className="bg1">
          <h1>RNR</h1>
         </div>
         <div className="products1">
            <div className="product1">
              <img src={p} alt="" />
            </div>
            <div className="product1"><img src={p} alt="" /></div>
            <div className="product1"><p>Sonamasoori and rnr crops are grown twice in a year in karif and rabi season in fertile black soil surrounded by krishna,Tunga and Bheema rivers which brings surplus yeild.</p></div>
         </div>
         <div className="bg2">
          <h1>Product 2</h1>
         </div>
         <div className="products2">
            <div className="product2"><img src={p} alt="" /></div>
            <div className="product2"><img src={p} alt="" /></div>
            <div className="product2"><p>Sonamasoori and rnr crops are grown twice in a year in karif and rabi season in fertile black soil surrounded by krishna,Tunga and Bheema rivers which brings surplus yeild.</p></div>
         </div>
      </div>
   );
};

export default Product;
